.navbar {
  position: fixed;
  top: 1%;
  left: 50%;
  transform: translateX(-50%);
  width: 92%;
  display: block;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-color: #4285F4;
  z-index: 1000;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.nav-header {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.nav-links {
  display: flex;
  gap: 1.5rem;
  justify-content: space-evenly;
}
  
.nav-links a {
  text-decoration: none;
  color: inherit;
  font-weight: bold;
}
  
/* Dark Mode Toggle */
.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #f4f4f4;
  transition: 0.4s;
  border-radius: 20px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 3px;
  bottom: 3px;
  background-color: #FBBC04;
  transition: 0.4s;
  border-radius: 50%;
}

.navtitle {
  padding-left: 7.5%;
  width: 80%;
  justify-items: center;
}

.toggle-container {
  width: 5%;
  justify-items: center; 
}
  
input:checked + .slider {
  background-color: #222222;
}

input:checked + .slider:before {
  transform: translateX(20px);
  background-color: #ccc;
}